.MovieAboutUs {
    padding: 20px 0px 30px 0px;
    color: #fff;
    background-color: rgb(12, 11, 11);
}
.actor__design{
    width: 90%;
    height: 100%;
    z-index: -1;
  
    
    margin: 0px auto;
}
.linkDescription{
    width: 90%;
    padding:10px;
    margin: 0px auto;
    display: -ms-flexbox;
    display: flex;
    color: #fff;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    background-color: orange;
    font-size: 15px;
}
.linkDescription:hover{
    text-decoration: underline;
    color: #fff;
}



