.rmdbLoadMoreBtn{
  background: #f3702f;
  width: 270px;
  height: 40px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 3%;
  margin: 0px auto;
}

.rmdbLoadMoreBtn p {
  font-family: 'Abel', sans-serif;
  font-size: 20px;
  padding: 5px;

}

.rmdbLoadMoreBtn:hover {
  opacity: 0.8;
}

@media screen and (max-width: 598px){
  .rmdbLoadMoreBtn{
    width: 200px;
  }

}


