.footer{
    background-color: #131313;
  
    text-align: center !important;
    color: #fff;
   /*border-top: 1px solid #2f2f2f;*/
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    padding: 20px auto;
}

.rowDesign{
    margin: 0 auto;
    text-align: center !important;
    padding-top: 18px;
}
.rowDesign1{
    margin: 0 auto;
    text-align: center !important;
    padding-top: 5px;
}
.rowDesign ul{
    display: inline !important;
    text-align: center;    
    padding-left: 0px !important;
}
.rowDesign li{
    display: inline;
    margin-right: 5px;
    list-style: none;
    font-family: 'Abel',sans-serif;
    font-size: 18px;
    font-weight: 600;
    text-shadow: 0 2px 2px rgba(0,0,0,.4);
}
.rowDesign ul span li a{
    font-size: 14px;
    font-weight: 600;
    color:#919191;
    font-family: Arial, Helvetica, sans-serif !important;
}
.rowDesign a{
    color: #fff;
    text-decoration: none !important;
}



@media (max-width: 768px){
    .rowDesign li{
        font-size: 15px;
    }
     
    .rowDesign ul span li a{
        font-size: 12px;
    }
    .rowDesign1 span li{
        font-size: 15px;
    }
    .no_row-design{
        display: none;
    }
  
}



