.movie__thumb-container{
  width: 90%;
  height: 100%;
  z-index: -1;
  background: rgb(37, 36, 36);
  padding: 10px 0px;
  margin: 10px auto;
}
.movie__info-container{
  margin: 0px 15px;
  letter-spacing: 0.5px;
}

.movie__info-details h1{
  font-size: 25px;
  margin:0px;
}
.imdb{
  width: 100%;
  background-color: #f3ce13 ;
  margin-right: 7px;
  padding: 2px 5px;
  text-align: center;
  border-radius: 5%;
 

  font-weight: bold;
}
.movie__info-details h5{
  font-size: 15px;
  margin:0px;
  letter-spacing: 0.5px;
}

.movie__info-thumb{
  width: 60%;
  overflow: hidden;
  margin: 10px 0px;
}

.movie__info-genres{
  display: block;
}
.color_white{
  font-size: 18px;
  margin-right: 1px;
  color: #fff !important;
}
.alignment__element{
  color: #f3702f;
  margin-right: 5px;
  font-weight: 600;
}

.movie__info-buttons{
  display: -ms-flexbox;
  display: flex;
  margin: 15px auto;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
 
}


.movie__thumb-btn{
  width: 140px;
  margin: 0px 10px;
  
  color: #fff;
  display: inline-block;
  font-weight: 400;
 
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #f3702f;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
  border: none;
  outline: none;
  -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  -o-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
}

.movie__thumb-btn:hover,
.movie__thumb-btn:active{
  background: #db6327 !important;
  border: none;
  outline: none;
  
}

.movie__video-container{
  width: 90%;
  height: 100%;
  z-index: -1;
  margin: 10px auto;
  margin-bottom: 20px;
}
.movie__video-design{
  width: 100%;
  height: 100%;
}

.movie__video-content{
  width: 100%;
  height: 190px;
}
.movie__video-description{
  font-size: 22px;
 
  letter-spacing: 1px;
}





