.toolbar{
    
    width: 100%;
    height: 56px;
    top:0;
    left:0;
    background-color: #232323;
    -webkit-box-shadow:0 0 5px rgba(0,0,0,.2);
            box-shadow:0 0 5px rgba(0,0,0,.2);
}
.toolbar__navigation{
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 1rem;
}
.toolbar__logo{
    margin-left: 1rem;
}
.toolbar__logo a{
    color: #fff;
    text-decoration: none;
    font-size: 1.5rem;
}

.spacer{
  -ms-flex:1 1;
      flex:1 1;  
}

.toolbar__navigation-items ul{
    list-style: none;
    margin:0;
    padding:0;
    display: -ms-flexbox;
    display: flex;
}
.toolbar__navigation-items li{
    padding: 0 0.5rem;
}

.toolbar__navigation-items a{
    color: #fff;
    text-decoration: none;
}
.toolbar__navigation-items a:hover,
.toolbar__navigation-items a:active{
    color: #fa923f;
}

.logo {
    width: 180px;
}

@media (max-width: 768px){
    .toolbar__navigation-items{
        display:none;
    }
    .logo {
        width: 130px;
    }
}

@media (min-width: 769px){
    .toolbar__toggle-button{
        display: none;
    }
    .toolbar__logo{
        margin-left: 0;
    }
    
}


