.pagination_container{
    display: -ms-flexbox;
    display: flex;
    width:90%;
    height: 100%;
    margin: 10px auto;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

}
.pagination{
    display: block ;
    list-style: none;
    font-size: 15px;
    text-align: center;
  
}
.pagination li{
    
    display: inline-block;
    
}
.pagination a{
    text-decoration: none;
    padding: 6px 7px;
    margin-right: 5px;
    border-radius: 8%;
    color: #fff;
}
.pagination a:hover,
.pagination a:active {
    
    background-color: #ff722c;
 
}
.link_active{
    background-color: #ff722c;
 
}


