.toggle-button{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 30px;
    width: 30px;
    background: transparent;
    border:none;
    cursor: pointer;
    padding: 6px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 10%;
    background: #444;
}

.toggle-button:focus{
    outline: none;
}

.toggle-button__line{
    width: 18px;
    height: 2px;
    background: white;
}


