
.rmdbSearchBar {
  width: 100%;
  height: 60px;
  background:#222;
  position: relative;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff; 
  margin-top:-1px;
}

.rmdbSearchBarContent{
  max-width: 1280px;
  width: 100%;
  height: 35px;
  
  background: #444;
  margin: 0 auto;
  border-radius: 40px;
  color: #fff;
  -ms-flex-pack: center;
      justify-content: center;
}



.rmdbSearchBarInput {
  font-family: 'Abel', sans-serif;
  font-size: 20px;
  padding-left: 15px;
  
  border: 0;
  background: transparent;
  height: 35px;
  width: 85%;
  font-weight: 400;
  color: #fff;
  float: left;
}
.icon__search{
  float: right;
  width: 15%;
  margin: 8px 10px;
  font-size: 18px;
}

.rmdbSearchBarInput:focus {
  outline: none;
}

@media (max-width: 768px) {
  .rmdbSearchBarInput {
    font-size: 22px;
    color: #fff;
    padding-bottom: 3px;
  }
}


